  grid-template-areas: "icon label" "icon subLabel";
}
.multilabel__container--stack .wrapper__icon {
  margin-right: var(--multi-label-stack-icon-space);
}
.multilabel__container--stack .wrapper__label {
  font-size: var(--multi-label-stack-label-size);
  font-weight: var(--multi-label-stack-label-weight);
}
.multilabel__container--stack .wrapper__subLabel {
