  font-weight: var(--multi-label-stack-sub-label-weight);
}
.multilabel__container--stack.multilabel__container--top-icon .wrapper__icon {
  margin-right: var(--multi-label-stack-top-icon-space);
}
.multilabel__container--inline {
  grid-template-rows: 1fr;
  grid-template-columns: auto auto 1fr;
  grid-template-areas: "icon label subLabel";
}
.multilabel__container--inline .wrapper__icon {
  margin-right: var(--multi-label-inline-icon-space);
}
.multilabel__container--inline .wrapper__label {
  font-size: var(--multi-label-inline-label-size);
  font-weight: var(--multi-label-inline-label-weight);
